import Vue from 'vue';
import Router from 'vue-router';
import ListDetail from '@/views/ListDetail';
import ListDetailComment from '@/views/ListDetailComment';
// import ListDetailSubmit from '@/views/ListDetailSubmit'
import ListDetailInfo from '@/views/ListDetailInfo'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/list'   //   
    },
    {
      path: '/list',
      name: 'list',
      meta: {
        title: '',
        keepAlive: true // 
      },
      component: resolve => require(['@/views/List'], resolve),
      children: [
        {
          path: 'classify',
          name: 'classify',
          meta: {
            title: '',
            keepAlive: true // 
          },
          components: {
            detail: resolve => require(['@/views/ListClassify'], resolve)
          },
          children: [
            {
              path: 'detail/:id',
              name: 'classdetail',
              meta: {
                title: '',
                keepAlive: true // 
              },
              component: ListDetail
            }
          ]
        },
        {
          path: 'detail/:id',
          name: 'listdetail',
          meta: {
            title: '',
            keepAlive: true // 
          },
          components: {
            detail: ListDetail
          },
          children: [
            {
              path: 'comment',
              name: 'Listdetailcomment',
              meta: {
                title: '',
                keepAlive: true
              },
              component: ListDetailComment
            },
            {
              path: 'info',
              meta: {
                title: '',
                keepAlive: true
              },
              component: ListDetailInfo
            },
            {
              path: 'submit',
              name: 'submit',
              meta: {
                keepAlive: true,
                title: ''
              },
              component: resolve => require(['@/views/ListDetailSubmit'], resolve),
              children: [
                {
                  path: 'info',
                  meta: {
                    title: '',
                    keepAlive: true
                  },
                  component: ListDetailInfo
                },
                {
                  path: 'submitorder',
                  name: 'submitorder',
                  meta: {
                    keepAlive: true,
                    title: ''
                  },
                  component: resolve => require(['@/views/state/Order'], resolve)
                }
              ]
            }
          ]
        }
      ]
    },
    {
      path: '/store',
      name: 'store',
      meta: {
        title: '',
        keepAlive: true
      },
      component: resolve => require(['@/views/ListDetailStore'], resolve),
      children: [
        {
          path: 'info',
          meta: {
            title: '',
            keepAlive: true
          },
          component: ListDetailInfo
        }
      ]
    },
    {
      path: '/shopping',
      name: 'shopping',
      meta: {
        title: '',
        keepAlive: true // 
      },
      component: resolve => require(['@/views/Shopping'], resolve)
    },
    {
      path: '/mycenter',
      name: 'mycenter',
      meta: {
        title: '',
        keepAlive: true // 
      },
      component: resolve => require(['@/views/MyCenter'], resolve),
      children: [
        {
          path: 'cash',
          meta: {
            title: '',
            keepAlive: true // 
          },
          component: resolve => require(['@/views/my/Cash'], resolve)
        },
        {
          path: 'collect',
          meta: {
            title: '',
            keepAlive: true // 
          },
          component: resolve => require(['@/views/my/Collect'], resolve)
        },
        {
          path: 'message',
          meta: {
            title: '',
            keepAlive: true // 
          },
          component: resolve => require(['@/views/my/Message'], resolve)
        },
        {
          path: 'friend',
          meta: {
            title: '',
            keepAlive: true // 
          },
          component: resolve => require(['@/views/my/Friend'], resolve)
        },
        {
          path: 'location',
          meta: {
            title: '',
            keepAlive: true // 
          },
          component: resolve => require(['@/views/my/Location'], resolve),
          children: [
            {
              path: 'form',
              meta: {
                title: '',
                keepAlive: true // 
              },
              component: resolve => require(['@/views/my/LocationForm'], resolve)
            }
          ]
        },
        {
          path: 'orderbox',   // 
          meta: {
            title: '',
            keepAlive: true // 
          },
          component: resolve => require(['@/views/order/OrderBox'], resolve),
          children: [
            {
              path: 'all',
              name: 'all',
              meta: {
                title: '',
                keepAlive: true // 
              },
              components: {   // 
                main: resolve => require(['@/views/order/All'], resolve)
              }
            },
            {
              path: 'deliver',
              name: 'deliver',
              meta: {
                title: '',
                keepAlive: true // 
              },
              components: {
                main: resolve => require(['@/views/order/Deliver'], resolve)
              }
            },
            {
              path: 'take',
              name: 'take',
              meta: {
                title: '',
                keepAlive: true // 
              },
              components: {
                main: resolve => require(['@/views/order/Take'], resolve)
              }
            },
            {
              path: 'remain',
              name: 'remain',
              meta: {
                title: '',
                keepAlive: true // 
              },
              components: {
                main: resolve => require(['@/views/order/Remain'], resolve)
              }
            },
            {
              path: 'after',
              name: 'after',
              meta: {
                title: '',
                keepAlive: true // 
              },
              components: {
                main: resolve => require(['@/views/order/After'], resolve)
              }
            },
            {
              path: 'return',
              name: 'return',
              meta: {
                title: '',
                keepAlive: true // 
              },
              components: {
                else: resolve => require(['@/views/order/OrderReturn'], resolve)
              },
              children: [
                {
                  path: 'audit',
                  meta: {
                    title: '',
                    keepAlive: true // 
                  },
                  component: resolve => require(['@/views/state/Audit'], resolve),
                  children: [
                    {
                      path: 'afterdetails',
                      meta: {
                        title: '',
                        keepAlive: true // 
                      },
                      component: resolve => require(['@/views/order/AfterDetails'], resolve)
                    }
                  ]
                }
              ]
            },
            {
              path: 'succeed',    // 
              meta: {
                title: '',
                keepAlive: true // 
              },
              components: {
                else: resolve => require(['@/views/state/Succeed'], resolve)
              },
              children: [
                {
                  path: 'evaluate',
                  meta: {
                    title: '',
                    keepAlive: true // 
                  },
                  component: resolve => require(['@/views/order/RemainEvaluate'], resolve),
                  children: [
                    {
                      path: 'stateevaluate',
                      meta: {
                        title: '',
                        keepAlive: true // 
                      },
                      component: resolve => require(['@/views/state/Evaluate'], resolve)
                    }
                  ]
                }
              ]
            }
          ]
        }
      ]
    }
  ]
})



